body .current {
  color: #b3ff00;
}

html {
  background-color: #d08a8a;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

.burger-text {
  position: absolute;
  font-size: 30px;
  text-align: center;
  top: 2px;
  left: 14px;
}

nav a:hover {
  background-color: gray;
}

nav {
  background-color: #c69c62;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  opacity: 1;
  z-index: 2;
}


.nav-link {
  color: black;
  text-decoration: none;
  float: left;
  display: block;
  width: 15.5%;
  height: 25px;
  border-width: 3px 3px 3px 0px;
  border-style: solid; 
  border-color: black;
  text-align: center;
  padding: 10px 0;
  opacity: 1;
  font-size: 22px;
  font-weight: bold;
}

.hamburger {
  color: black;
  float: left;
  display: block;
  width: 50px;
  height: 25px;
  border: 3px solid black;
  padding: 10px 0;
  opacity: 1;
}

p {
  font-size: 25px;
  width: 800px;
}

h1 {
  font-size: 45px;
}

.background {
  font-size: 16px;
  position: fixed; 
  width: 640px;
  //white-space: pre;
  color: #ad7222e0;
  z-index: -1;
  left: 50px;
  transform: rotate(-20deg);
  margin-top: 130px;
}

body {
  margin-top: 50px;
  height: 1500px;
}

.text-content {
  z-index: 1;
}

.side-nav {
  //display: hidden;
  position: fixed;
  left: 0px;
  height: 240px;
  width: 200px;
  bottom: 51px;
  border: 2px solid black;
}

.side-nav-links {
  text-decoration: none;
  color: black;
  display: block; 
  height: 40px;
  width: 200px;
  border: 1px solid black;
}

@media only screen and (min-width:1260px) {
  nav {
    width:1200px; 
    left: 50%; 
    margin-left: -600px;  
  }  

  .nav-link  {
    width: 15.631%;
  } 
  
  .background {
    left: 50%;
    margin-left: -550px;
  }
  
  .text-content {
    left: 50%;
    margin-left: -550px;
    position: relative;
    //display: inline-block;
    width: 950px;
  }
}

@media only screen and (max-width:1200px) {
  .nav-link {
    //width: 15%;
  }
}

@media only screen and (max-width:1060px) {
  .nav-link {
    width: 15%;
  }

  .background {
    font-size: 14px;
  }
}

@media only screen and (max-width:850px) {
  .background {
    font-size: 12px;
  }
}

@media only screen and (max-width:760px) {
  .nav-link {
    width: 14.631%;
  }
}

@media only screen and (max-width:700px) {
  .nav-link {
    display: none;
  }
  
  .background {
    transform: rotate(-75deg);
    margin-top: 180px;
    left: 0px;
  }
}

@media only screen and (max-width:414px) {
  .background {
    font-size: 10px;
    left: -140px;
    transform: rotate(-80deg);
    margin-top: 85px;
  } 

  .nav {
    top: inherit;
    bottom: 1px;
  }

  //#side-nav {
  //  display: inherit;
  //}
}

@media only screen and (max-width:375px) {
   .background {
    font-size: 10px;
    left: -140px;
    transform: rotate(-80deg);
    margin-top: 85px;
  }

  .nav {
    top: inherit;
    bottom: 1px;
  }
}

@media only screen and (max-width:320px) {
  .background {
    font-size: 9px;
    margin-top: 20px;
  }
}
